import React, { useEffect, useState } from 'react';
import Collapsible from 'react-collapsible';

import Select from 'react-select';
import { Joke } from './Joke';
import { useForm } from './useForm';



export const App = () => {
	
	const [response, setResponse] = useState({});
	const [selectedOption, setSelectedOption] = useState(null);
	const [options, setOptions] = useState(null);
	const [ formLoginValues , handleFormChange] = useForm({
        word:'',
        page:'1',
		limit:'10',
		email:''
    })
	const { word, page, limit, email } = formLoginValues

	
	const categoriesLoad = async ()=>{
		const request = await fetch('https://api.chucknorris.io/jokes/categories');
		const response = await request.text();
		const data = JSON.parse(response)
		
		setOptions(data.map(e=>({ value: e, label: e })))

	}
	useEffect( () => {
		categoriesLoad()
	}, [])
	
	
	const handleChangeSelect = (selectedOption) => {
	   setSelectedOption(selectedOption) 
	};
	const handleRandomSubmit = async () => {

		const url = 'http://localhost/api/jokes/random'
		const request = await fetch(url);
		const data = await request.text();
		setResponse({data:JSON.parse(data), url})
	}
	const handleCategorySubmit = async () => {
		const url = `http://localhost/api/jokes/random?category=${selectedOption.value}`

		const request = await fetch(url);
		const data = await request.text();
		setResponse({data:JSON.parse(data), url})
	}
	const handleLogin = async(e) => {
		e.preventDefault()

		const url = `http://localhost/api/jokes/search?query=${word}&page=${page}&limit=${limit}&email=${email}`
		console.log(url);
		const request = await fetch(url);
		const data = await request.text();
		setResponse({multipleData:JSON.parse(data), url})
    }
	const handleNextPage = ()=>{
		const pageHtml = { target:document.getElementById("page")}
		pageHtml.target.value = parseInt(page) + 1;
		handleFormChange(pageHtml)
		setTimeout(() => {
			document.getElementById('button').click()
		}, 2000);
	}
	const handlePreviousPage = ()=>{
		const pageHtml = { target:document.getElementById("page")}
		pageHtml.target.value = parseInt(page) - 1;
		handleFormChange(pageHtml)
		setTimeout(() => {
			document.getElementById('button').click()
		}, 2000);
	}
	return (
		<div className="container">
			<h1 className="title">AcidTango</h1>
			<div className="row">
				<div className="col flex">
					<h2>Random Joke</h2>
					<button
					className='btn btn-primary'
						onClick={handleRandomSubmit}
					>Random Joke</button>
				</div>
				<div className="col flex">
					<h2> By Category</h2>

					<Select
						className='select mt-2 mb-2'
						value={selectedOption}
						onChange={handleChangeSelect}
						options={options}
					/>
					<button
					className='btn btn-primary'
						onClick={handleCategorySubmit}
					>Random Joke From Category
					</button>
				</div>
				<div className="col flex">
					<h2>By word</h2>
					<form onSubmit={handleLogin}>
                        <div className="form-group mt-2">
							<label htmlFor="word">Word</label>
                            <input 
                                type="text"
                                className="form-control"
                                placeholder="Word"
                                name="word"
                                value={word}
                                onChange={ handleFormChange }
                            />
                        </div>
                        <div className="form-group mt-2">
							<label htmlFor="number"> Page</label>
                            <input 
                                type="number"
                                className="form-control"
                                placeholder="Page"
                                name="page"
								id='page'
                                value={page}
                                onChange={ handleFormChange }
                            />
                        </div>
                        <div className="form-group mt-2">
							<label htmlFor="number"> Limit</label>
                            <input 
                                type="number"
                                className="form-control"
                                placeholder="Limit"
                                name="limit"
                                value={limit}
                                onChange={ handleFormChange }
                            />
                        </div>
                        <div className="form-group mt-2">
							<label htmlFor="number"> Email</label>
                            <input 
                                type="email"
                                className="form-control"
                                placeholder="Your Email"
                                name="email"
                                value={email}
                                onChange={ handleFormChange }
                            />
							<p className='optional'>Optional: You will recieve an email whit the data</p>
                        </div>
                        <div className="form-group">
                            <input 
							 	id='button'
                                type="submit"
                                className="btn btn-primary mt-2"
                                value="Search" 
                            />
                        </div>
                    </form>
				</div>
			</div>

			<div className='container flex mt-3'>
				<h2>Response</h2>
				{
					response.url && <a href={response.url} rel="noreferrer" target="_blank">Request link</a>
				}
				{
					response.data &&
					( 
						<Joke data={response.data}/>
					)
				}
				{
					response.multipleData &&
					( 
						<div className='joke_container'> 
							{
								response.multipleData.email && <p>Sent to: {response.multipleData.email}</p>
							}
							<p>Search: {response.multipleData.search}</p>
							<a href={response.multipleData.linkOfTheOriginalAPI} rel="noreferrer" target="_blank">Original link</a>
							<p>Page: {response.multipleData.actualPage} of {response.multipleData.numOfPages}</p>
							{
								response.multipleData.previousPage &&
								( 
									<button 
										className='btn btn-outline-primary'
										onClick={handlePreviousPage}
									>Previous Page</button>
								)
							}
							{
								response.multipleData.nextPage &&
								( 
									<button 
										className='btn btn-outline-primary Next'
										onClick={handleNextPage}
									>Next Page</button>
								)
							}
							<h2>Jokes</h2>
							{response.multipleData.jokes.map(e=> <Joke key={e.id} data={e}/>)}
						</div>
					)
				}
				<Collapsible trigger="Raw Data" triggerClassName='btn btn-outline-info' triggerOpenedClassName='btn btn-outline-info'>
					<pre className='contains' >{JSON.stringify(response, null, 2)}</pre>
				</Collapsible>

			</div>
		</div>

	);
};
