import React from 'react'

export const Joke = ({data}) => {
  return (
    <div className='joke_container'>
        <h2 className='joke_title'>Joke</h2>
        <img src={data.icon_url} alt="icon" />
        {data.categories.length > 0 && <p>category: {data.categories}</p>}
        <p>{data.value}</p>
        <a href={data.url} rel="noreferrer" target="_blank">Joke link</a>
    </div>
  )
}
